}

.order-btn {
  appearance: none;
  color: #fff;
  background: 0;
  border: 0;
  border-radius: .5rem;
  padding: .75rem 1.5rem;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 500;
  position: relative;
  bottom: 10%;
