:root {
  --bgcolor: #262529;
  --clockheight: 220px;
  --radius: 30px;
  --rotationtime: .55s;
  --avobg: #e3f668;
}

.action-description, .action-title {
  text-align: center;
  margin-bottom: 40px;
  font-size: 40px;
}

.action-wraper {
  height: calc(var(--clockheight)  + 150px);
  background-color: var(--bgcolor);
  color: #fff;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

.clock {
  grid-column-gap: 8px;
  min-width: 100px;
  height: var(--clockheight);
  border-radius: var(--radius);
  background-image: linear-gradient(#0e0e0f 0%, #1a191c 20%, #2c2c34 50%, #14141b 100%);
  grid-template-columns: 1fr 1fr 1fr 1fr;
  display: grid;
  box-shadow: inset 0 -3px 6px 3px #0003, inset 0 4px 8px 3px #0006, 0 2px 3px 1px #ffffff4d, 0 -2px 4px 4px #38383d80;
}

@media screen and (width >= 768px) {
  .clock {
    grid-column-gap: 10px;
    min-width: 150px;
    padding: 0 12px;
  }
}

@media screen and (width >= 1200px) {
  .clock {
    grid-column-gap: 12px;
    min-width: 200px;
  }
}

.flipper {
  width: 100%;
  min-width: 100px;
  transform-style: preserve-3d;
  perspective: 1600px;
  height: 100%;
  position: relative;
}

@media screen and (width >= 768px) {
  .flipper {
    min-width: 180px;
  }
}

@media screen and (width >= 1200px) {
